.form__form-info {
  border: none;
  border-bottom: 1px solid $gray;
  margin: 0;
  margin-top: 24px;
  padding: 0 20px;
  padding-bottom: 23px;
}
.form__label {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin-top: 14px;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: $darkest-gray;
}
.form__program-item {
  margin-bottom: 23px;
}
.form__input {
  flex-grow: 2;
  border: 1px solid $light-gray;
  min-height: 45px;
  padding: 10px 10px 10px 12px;
  text-transform: uppercase;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: $darkest-gray;
}
.form__info-text {
  padding-right: 16px;
}
.form__form-program {
  border: none;
  border-bottom: 1px solid $gray;
  padding: 0 20px;
  padding-bottom: 2px;
}
.form__program-label {
  margin-top: 24px;
}
.form__program-list {
  padding: 0;
  padding-left: 43px;
  list-style: none;
  margin-top: 30px;
}
.form__program-label::before {
  content: "";
  position: absolute;
  left: -45px;
  top: -6px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid $gray;
  cursor: pointer;
}
.form__program-input:checked + .form__program-label::after {
  content: "";
  position: absolute;
  left: -37px;
  top: 2px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  border: 1px solid $green;
  background: $green;
  cursor: pointer;
}
.form__form-contact {
  border: none;
  padding: 0 18px;
}
.form__contact-text {
  padding-right: 11px;
}
.form__contact-title {
  margin-top: 27px;
  margin-bottom: 0;
}
.form__contact-wrapper {
  margin-top: 30px;
}
.form__contact-input {
  padding-right: 40px;
}
.form__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
  color: $black;
}
.form__comment-title {
  margin: 0;
}
.form__form-comment {
  margin-top: 0;
  margin-bottom: 0;
  border: none;
  padding: 29px 18px;
  padding-bottom: 0;
}
.form__form-title {
  margin: 0;
  margin-top: 21px;
}
.form__comment-text {
  margin-top: 36px;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: $darkest-gray;

  width: 100%;
  min-height: 150px;
  padding: 9px 12px 13px 15px;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  border: 1px solid $gray;
  resize: none;
  text-transform: uppercase;
}
.form__form-additional {
  border: none;
  padding: 0 18px;
}
.form__additional-title {
  margin: 0;
  margin-top: 21px;
}

.form__additional-list {
  padding: 0;
  padding-left: 46px;
  list-style: none;
  margin: 0;
  margin-top: 38px;
}
.form__additional-label {
  position: relative;
  margin-top: 24px;
}
.form__additional-label::before {
  content: "";
  position: absolute;
  left: -45px;
  top: -5px;
  width: 30px;
  height: 30px;
  border: 1px solid $gray;
  cursor: pointer;
}
.form__additional-checkbox:checked + .form__additional-label::after {
  content: "";
  position: absolute;
  left: -35px;
  top: 0px;
  width: 10px;
  height: 16px;
  border-top: 2px solid #68b738;
  border-left: 2px solid #68b738;
  transform: rotate(223deg);
  cursor: pointer;
}
.form__button {
  width: 280px;
  height: 40px;
  border: 1px solid $green;
  background-color: $green;
  color: $white;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 38px;
  text-align: center;
  text-transform: uppercase;
  margin: 0 auto;
  margin-top: 42px;
  display: block;
}
.form__text {
  margin-top: 19px;
  text-align: center;
  padding-bottom: 14px;
}
