.social {
  padding: 0;
  margin: 0;
  padding-bottom: 5px;
  border-bottom: 1px solid #d9d9d9;
  @media (min-width: $tablet-width) {
    border: none;
  }
}
.social__list {
  width: 130px;
  list-style: none;
  margin: 0 auto;
  margin-top: 21px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  @media (min-width: $tablet-width) {
    margin-top: 0;
    margin-left: 30px;
  }
}
.social__item {
  margin-bottom: 10px;
}

.social__logo--vk {
  @media (min-width: $tablet-width) {
    width: 30px;
    height: 22px;
  }
}
.social__logo--instagram {
  @media (min-width: $tablet-width) {
    width: 23px;
    height: 23px;
  }
}
.social__logo--facebook {
  @media (min-width: $tablet-width) {
    width: 28px;
    height: 33px;
  }
}
