.footer {
  background-color: $light-gray;
  background-color: $light-gray;
  padding: 19px 20px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  @media (min-width: $tablet-width) {
    flex-direction: row;
    padding: 39px 28px;
    align-items: center;
    justify-content: space-between;
  }
}
.footer__logo {
  padding: 20px 0;
  padding-bottom: 26px;
  text-align: center;
  border-bottom: 1px solid #d9d9d9;
  @media (min-width: $tablet-width) {
    border: none;
  }
}
